    import { mapState ,mapMutations } from 'vuex'; 
	export default{
		components: {
			
		},
		data() {
			return {
				money: 0,
				user_data:{},
				user_cms_data:{},
				user_cms_user_payment:{},
				logining: false
			};
		},
		onLoad(){
			this.query();
			this.query_payment();
		},
        computed: {
			...mapState(['hasLogin','userInfo'])
		},
		methods:{
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navTo(url){
				if(!this.hasLogin){
					url = '/pages/public/login';
				}
				uni.navigateTo({  
					url
				})  
			}, 
			//规格弹窗开关
			toggleSpec() {
				if(this.specClass === 'show'){
					this.specClass = 'hide';
					setTimeout(() => {
						this.specClass = 'none';
					}, 250);
				}else if(this.specClass === 'none'){
					this.specClass = 'show';
				}
			},
			//选择规格
			selectSpec(index, pid){
				let list = this.specChildList;
				list.forEach(item=>{
					if(item.pid === pid){
						this.$set(item, 'selected', false);
					}
				})

				this.$set(list[index], 'selected', true);
				//存储已选择
				/**
				 * 修复选择规格存储错误
				 * 将这几行代码替换即可
				 * 选择的规格存放在specSelected中
				 */
				this.specSelected = []; 
				list.forEach(item=>{ 
					if(item.selected === true){ 
						this.specSelected.push(item); 
					} 
				})
			},
			payment(){
				this.logining = true;
				const {money} = this;
				
				this.$http.post('/cms/member/payment/payment', {paymentMoney:money}).then(res => {
					this.$api.msg(res.info.status_err);
					this.logining = false;
				}).catch(err => {});
			},
			query_payment(){
				this.$http.post('/cms/member/payment/account', {}).then(res => {
					if(res.data.item){
						this.user_cms_user_payment = res.data.item;
					}
				}).catch(err => {});
			},
			query() {
				this.$http.post('/cms/member/info/getdata', {}).then(res => {
					if(res.data.user_data){
						this.user_data = res.data.user_data;
					}
					if(res.data.user_cms_data){
						this.user_cms_data = res.data.user_cms_data;
					}
				}).catch(err => {});
			},
			stopPrevent(){}
		},

	}
